import {Dialog,showDialog} from "./dialog.js";
import {miesiacF, PlaceType} from "./panel_platnosci.js";
const { useEffect,useState,createRef,useCallback,cloneElement } = React


export class DialogAttenderManipulateGroupChanges extends React.Component {
    attenderId = -1;
    state = {payments:null,season:null,closedMonths:null,place:null,biggestCycleNum:null}
    constructor(props) {
        super(props)
        this.attenderId = props.attenderId

        const errorF = (err)=>{
            console.log(err)
            injectRField(this,"error")(err)
        }

        gather(`uczestnicy/${this.attenderId}/payments/fresh`,injectRField(this,"payments"),errorF)
        gather(`uczestnicy/${this.attenderId}/season`,injectRField(this,"season"),errorF)
        gather(`uczestnicy/${this.attenderId}/closedMonths`,injectRField(this,"closedMonths"),errorF)
        gather(`uczestnicy/${this.attenderId}`,attenderData=>{
            injectRField(this,"attender")(attenderData)

            gather(`uczestnicy/${this.attenderId}/miejsce`,placeData=>{
                injectRField(this,"place")(placeData)

                const rodzaj = placeData.rodzaj
                if(rodzaj == PlaceType.TYPE_ADULTS) {
                    gather(`grupy/${attenderData.groupId}/biggestCycleNum`,injectRField(this,"biggestCycleNum"),errorF)
                }
            },errorF)
        },errorF)
        //gather(`uczestnicy/${this.attenderId}/at`,this,"place",errorF)
    }
    dialog = createRef()

    render() {
        if(this.state.payments == null)
            return null
        if(this.state.season == null)
            return null
        if(this.state.closedMonths == null)
            return null
        if(this.state.place == null) {
            return null
        }

        const monthsRows = []
        const attenderId = this.attenderId

        const placeType = this.state.place.rodzaj
        if(placeType == PlaceType.TYPE_ADULTS) {
            if(this.state.biggestCycleNum == null) {
                return null
            }
            const biggestCycleNum = this.state.biggestCycleNum

            for(let i = 1; i < biggestCycleNum+1; i++) {
                const {status,amountPaid,amountToPay,closed} = this.state.payments[i]
                const cm = this.state.closedMonths.find(({id,season,month,year,cycle,amount})=>cycle === i)

                function AlterPaymentCell(props) {
                    const [cm,setCM] = useState(props.closedMonth)

                    const [locked,setLocked] = useState(cm != null)
                    const [amount,setAmount] = useState(locked && cm != null ? cm.amount : props.freshAmount)

                    const [altering,setAlteringMode] = useState(false)

                    const [processing,setProcessing] = useState(false)

                    const inp = createRef()

                    if(altering) {
                        return <div className={`alter_payment_el${processing ? " processing" : ""}`}>
                            <input type="number" defaultValue={amount} ref={inp}/>
                            <i className="fa-solid fa-floppy-disk" onClick={()=>{
                                if(inp.current == null) {
                                    return
                                }
                                const inputValue = inp.current.value
                                setProcessing(true)
                                fet("closedMonths", {
                                    method: "POST", body: JSON.stringify({
                                        attenderId: attenderId,
                                        cycle: props.cycleNum,
                                        amount: inputValue
                                    })
                                }).then(res => {
                                    setProcessing(false)
                                    setCM(res)
                                    setAmount(inputValue)
                                    setLocked(true)
                                    setAlteringMode(false)
                                }).catch(err => {
                                    setProcessing(false)
                                    console.log(err)
                                })
                            }}></i>
                            <i className="fa-solid fa-trash" onClick={()=>{
                                if(cm == null) {
                                    setAlteringMode(false)
                                    return
                                }
                                setProcessing(true)
                                fet(`closedMonths/${cm.id}`, {method: "DELETE"}).then(res => {
                                    setProcessing(false)
                                    setCM(null)
                                    setAmount(props.freshAmount)
                                    setLocked(false)
                                    setAlteringMode(false)
                                }).catch(err => {
                                    setProcessing(false)
                                    console.log(err)
                                })
                            }}></i>
                            <i className="fa-solid fa-x" onClick={()=>{
                                setAlteringMode(false)
                            }}></i>
                        </div>
                    }

                    function quickcopy() {
                        setProcessing(true)
                        fet("closedMonths", {
                            method: "POST", body: JSON.stringify({
                                attenderId: attenderId,
                                cycle: props.cycleNum,
                                amount: props.freshAmount
                            })
                        }).then(res => {
                            setProcessing(false)
                            setCM(res)
                            setAmount(props.freshAmount)
                            setLocked(true)
                            setAlteringMode(false)
                        }).catch(err => {
                            setProcessing(false)
                            console.log(err)
                        })
                    }

                    return locked ?
                        <div className={`alter_payment_el${processing ? " processing" : ""}`}>
                            <span>ustalona: {amount}</span>
                            <input type="button" value="zmień" onClick={()=>{
                                setAlteringMode(true)
                            }}/>
                            <input className="quickcopy" type="button" value="<" onClick={quickcopy}/>
                        </div>
                        :
                        <div className={`alter_payment_el${processing ? " processing" : ""}`}>
                            <span>nieustalona</span>
                            <input type="button" value="ustal" onClick={()=>{
                                setAlteringMode(true)
                            }}/>
                            <input className="quickcopy" type="button" value="<" onClick={quickcopy}/>
                        </div>
                }

                monthsRows.push(
                    <tr key={i}>
                        <td>Cykl {i}</td>
                        <td className="cm_cell"><AlterPaymentCell cycleNum={i} closedMonth={cm} freshAmount={amountToPay}/></td>
                        <td>{amountToPay}</td>
                    </tr>
                )
            }

        } else {
            const startingMonth = parseInt(this.state.season['odStr'].split('-')[1])-1
            const fromFirstToLatestMonth = (funct) => {
                for(let i = 0; i < 12; i++) {
                    const monthIdx = (startingMonth+i)%12
                    funct(monthIdx)
                }

            }

            fromFirstToLatestMonth((monthIdx)=>{

                const {status,amountPaid,amountToPay,closed} = this.state.payments[monthIdx]
                const cm = this.state.closedMonths.find(({id,season,month,year,cycle,amount})=>month === monthIdx)

                function AlterPaymentCell(props) {
                    const [cm,setCM] = useState(props.closedMonth)

                    const [locked,setLocked] = useState(cm != null)
                    const [amount,setAmount] = useState(locked && cm != null ? cm.amount : props.freshAmount)

                    const [altering,setAlteringMode] = useState(false)

                    const [processing,setProcessing] = useState(false)

                    const inp = createRef()

                    if(altering) {
                        return <div className={`alter_payment_el${processing ? " processing" : ""}`}>
                            <input type="number" defaultValue={amount} ref={inp}/>
                            <i className="fa-solid fa-floppy-disk" onClick={()=>{
                                if(inp.current == null) {
                                    return
                                }
                                const inputValue = inp.current.value
                                setProcessing(true)
                                fet("closedMonths", {
                                    method: "POST", body: JSON.stringify({
                                        attenderId: attenderId,
                                        month: props.monthNum,
                                        amount: inputValue
                                    })
                                }).then(res => {
                                    setProcessing(false)
                                    setCM(res)
                                    setAmount(inputValue)
                                    setLocked(true)
                                    setAlteringMode(false)
                                }).catch(err => {
                                    setProcessing(false)
                                    console.log(err)
                                })
                            }}></i>
                            <i className="fa-solid fa-trash" onClick={()=>{
                                if(cm == null) {
                                    setAlteringMode(false)
                                    return
                                }
                                setProcessing(true)
                                fet(`closedMonths/${cm.id}`, {method: "DELETE"}).then(res => {
                                    setProcessing(false)
                                    setCM(null)
                                    setAmount(props.freshAmount)
                                    setLocked(false)
                                    setAlteringMode(false)
                                }).catch(err => {
                                    setProcessing(false)
                                    console.log(err)
                                })
                            }}></i>
                            <i className="fa-solid fa-x" onClick={()=>{
                                setAlteringMode(false)
                            }}></i>
                        </div>
                    }

                    function quickcopy() {
                        setProcessing(true)
                        fet("closedMonths", {
                            method: "POST", body: JSON.stringify({
                                attenderId: attenderId,
                                month: props.monthNum,
                                amount: props.freshAmount
                            })
                        }).then(res => {
                            setProcessing(false)
                            setCM(res)
                            setAmount(props.freshAmount)
                            setLocked(true)
                            setAlteringMode(false)
                        }).catch(err => {
                            setProcessing(false)
                            console.log(err)
                        })
                    }

                    return locked ?
                        <div className={`alter_payment_el${processing ? " processing" : ""}`}>
                            <span>ustalona: {amount}</span>
                            <input type="button" value="zmień" onClick={()=>{
                                setAlteringMode(true)
                            }}/>
                            <input className="quickcopy" type="button" value="<" onClick={quickcopy}/>
                        </div>
                        :
                        <div className={`alter_payment_el${processing ? " processing" : ""}`}>
                            <span>nieustalona</span>
                            <input type="button" value="ustal" onClick={()=>{
                                setAlteringMode(true)
                            }}/>
                            <input className="quickcopy" type="button" value="<" onClick={quickcopy}/>
                        </div>
                }

                monthsRows.push(
                    <tr key={monthIdx}>
                        <td>{miesiacF(monthIdx)}</td>
                        <td className="cm_cell"><AlterPaymentCell monthNum={monthIdx} closedMonth={cm} freshAmount={amountToPay}/></td>
                        <td>{amountToPay}</td>
                    </tr>
                )
            })
        }


        return <Dialog ref={this.dialog} title="rozliczenia uczestnika">
            <div className="dialog_attender_manipulate_payments">
            <table>
                <tbody>
                <tr key={"tit"}>
                    <th>{placeType == PlaceType.TYPE_ADULTS ? "Cykl" : "Miesiąc"}</th>
                    <th>kwota za ten miesiąc</th>
                    <th>wyliczone</th>
                </tr>
                {monthsRows}
                </tbody>
            </table>
                <input onClick={showHelp} value="pomoc" type="button"/>
            </div></Dialog>
    }
}

function showHelp() {
    function HelpDialog(props) {

        return <Dialog>
            <div title="pomoc">
                <p>ustalenie kwoty za dany miesiąc sprawia że staje się ona "statyczna" - żadna zmiana typu zmiana grupy zaj, zmiana rabatu, etc nie wpłynie na nią</p>
                <p>usunięcie "ustalenia kwoty" spowoduje że kwota w danym miesiącu stanie się "dynamiczna" - znowu będą na nią wpływać wszystkie zmiany</p>
                <p>kwota w kolumnie "wyliczone" jest kwotą wynikającą z "aktualnego stanu rzeczy" - jest to kwota policzona według dostępnych na ten moment danych</p>
                <p>użycie strzałki spowoduje ustalenie kwoty za dany miesiąc na kwotę z kolumny "wyliczone"</p>
                <p>program przy najbliższej możliwej okazji automatycznie zamknie wszystkie miesiące dla których może zostać wykonana weryfikacja płatności</p>
                <p>program NIE zamyka automatycznie miesięcy w których nie było żadnych zajęć</p>
                <p>program NIE zamyka automatycznie miesięcy jeżeli w dowolnym poprzedzającym miesiącu występuje zaległość</p>
            </div>
        </Dialog>
    }

    showDialog(<HelpDialog/>)
}

